.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

*,*::after,*::before{margin:0;padding:0;box-sizing:inherit}body{font-weight:normal;font-size:1rem;color:#191F1C;font-family:"Roboto",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-rendering:optimizeLegibility}h1,h2,h3,h4,h5,h6{font-family:"Playfair Display",serif,sans-serif}#sectionOne{display:flex;margin:4% 5%;justify-content:space-between}#sectionOne .sectionOne-item{width:30%;background:url(/GrowersBrains/static/media/greenhouse.ccbe5658.jpg) no-repeat center center/cover;min-height:450px;display:flex;align-items:center;justify-content:center}#sectionOne .sectionOne-item .card{width:70%;height:70%;background:#fff;overflow:hidden;border:1px solid #191F1C;position:relative}#sectionOne .sectionOne-item .card:hover{background:rgba(255,255,255,0.9)}#sectionOne .sectionOne-item .card .content-container{padding:2.5rem 1.5rem;text-align:left}#sectionOne .sectionOne-item .card .title{font-size:2rem}#sectionOne .sectionOne-item .card .description{padding-top:0.75rem}#sectionOne .sectionOne-item .card .link{display:block;text-decoration:none;cursor:pointer;padding-top:2rem;color:#191F1C;position:absolute;bottom:15px;left:24px}@media (max-width: 768px){#sectionOne{flex-direction:column}#sectionOne .sectionOne-item{width:100%}#sectionOne .sectionOne-item .card{position:inherit}#sectionOne .sectionOne-item .card .link{position:inherit}#sectionOne .sectionOne-item+.sectionOne-item{margin-left:0;margin-top:3rem}}.container{max-width:1100px;margin:0 auto}.sectionTwo-item{display:flex;padding:2rem}.sectionTwo-item .imgContainer{flex:5.7 1;position:relative}.sectionTwo-item .imgContainer img{height:360px;width:100%;object-fit:cover}.sectionTwo-item .content-container{flex:4.3 1;padding:4rem 3rem 1rem;text-align:left}.sectionTwo-item .content-container .title{margin-bottom:1rem;font-size:2rem}.sectionTwo-item .content-container .description{font-size:1.1rem}#sectionTwo *:nth-child(2){flex-direction:row-reverse}#sectionTwo *:nth-child(2) .content-container{padding-left:0}@media (max-width: 768px){.sectionTwo-item{display:flex}.sectionTwo-item .imgContainer{display:none}.sectionTwo-item .content-container{border:1px solid #57AF7E;text-align:center;margin:24px}}

